<template>
  <div class="contenedor">
    <div class="container-fluid bg-inverse"> 
        <nav class="navbar navbar-toggleable-md navbar-light bg-faded navbar-inverse bg-inverse container">
            <button class="navbar-toggler navbar-toggler-right" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <a class="navbar-brand" href="#">Domotica</a>

            <div class="collapse navbar-collapse" id="navbarSupportedContent">
                <div class="navbar-nav ml-auto">
                    <router-link :to="{name:'inicio'}" class="nav-item nav-link" active-class="active" exact>Inicio</router-link>
                    <router-link :to="{name:'logs'}" class="nav-item nav-link mr-4" active-class="active" exact>Historial</router-link>
                    <a href="#" class="btn btn-danger" @click="logout()">Cerrar Sesion</a>
                </div>
            
            </div>
        </nav>
    </div>
    <div>
        <router-view></router-view>
    </div>
  </div>    
</template>
<script>
import firebase from 'firebase'
export default {
  data(){
      return{

      }
  },
  methods:{
      logout(){
          firebase.auth().signOut().then(()=>{
              this.$router.replace('login');
          })
      }
  }
}
</script>
<style scoped>
div.contenedor{
    width: 100%;
    min-height: 100vh;
    background: #eee;
}
</style>




