<template>
  <div class="container mt-4">
      <table class="table">
  <thead>
    <tr>
      <th>#</th>
      <th>Email</th>
      <th>Accion</th>
      <th>Fecha</th>
      <th>Hora</th>
    </tr>
  </thead>
  <tbody>
    <tr v-for="log in logs">
      <th scope="row">{{log.numero}}</th>
      <td>{{log.email}}</td>
      <td>{{log.accion}}</td>
      <td>{{log.fecha}}</td>
      <td>{{log.hora}}</td>
    </tr>
  </tbody>
</table>
  </div>
</template>
<script>
import {db} from '../firebase'
export default {
  data(){
      return{
        logs:[]
      }
  },
  created(){
    db.ref('logs').on('value',snapshot=>{
      let cont = 0;
      this.logs=[];
      for(let key in snapshot.val()){
        cont++;
        this.logs.push({
          numero:cont,
          email:snapshot.val()[key].mail,
          accion:snapshot.val()[key].msj,
          fecha:snapshot.val()[key].fecha,
          hora:snapshot.val()[key].hora
        });
      }
    })
  }
}
</script>
<style scoped>
table{
    background: white;
    text-align: left;
}
</style>



