<template>
<div class="container">
 <form @submit.prevent="login()">
      <h3>INGRESAR</h3>
      <div class="form-group">
          <label>Email</label>
          <input type="email" class="form-control" placeholder="Ingrese email" v-model="user">
      </div>
      <div class="form-group">
          <label>Contraseña</label>
          <input type="password" class="form-control" placeholder="Ingrese contraseña" v-model="pass">
      </div>
      <input type="submit" value="INGRESAR" class="btn btn-success">
      <router-link to="/register">No tienes cuenta?</router-link>
  </form>
</div>
 
</template>
<script>
import firebase from 'firebase'
export default {
  data(){
      return{
          user:'',
          pass:''
      }
  },
  methods:{
      login(){
          if(this.user!='' && this.pass!=''){
              firebase.auth().signInWithEmailAndPassword(this.user,this.pass).then(
                  user => {
                      this.$router.replace('admin')
                  },
                  err => {
                      alert('Ops. '+err.message)
                  }
              )
          }
      }
  }
}
</script>
<style scoped>
.container{
    width: 500px;
    max-width: 95%;
    background: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
}
form{
    width: 400px;
    max-width: 90%;
    margin: 3em 0;
    
    text-align: left;
}
h3{
    text-align:center;
    margin-bottom: 1em;
    font-weight: bold;
}
.form-group{
    text-align: left;
}
label{
    margin-left: .5em;
    font-weight: bold;
}
input[type="submit"]{
    display: block;
    margin-bottom: 1em;
}
input[type="submit"]:hover{
    cursor: pointer;
}
</style>





