import Login from '../components/Login.vue'
import Register from '../components/Register.vue'
import Admin from '../components/Admin.vue'
import Medicion from '../components/Medicion.vue'
import Historial from '../components/Historial.vue'

export const routes=[
    {
        path:'*',
        redirect:'/login'
    },
    {
        path:'/',
        redirect:'/login'
    },
    {
        path:'/login',
        name:'login',
        component:Login
    },
    {
        path:'/register',
        name:'register',
        component:Register
    },
    {
        path:'/admin',
        component:Admin,
        children:[
            {
                path:'',
                name:'inicio',
                component:Medicion
            },
            {
                path:'logs',
                name:'logs',
                component:Historial
            }
        ],
        meta:{
            requiresAuth:true
        }
    }
]


