<template>
<div class="container">
    <form @submit.prevent="registrar()">
      <h3>REGISTRAR USUARIO</h3>
      <div class="form-group">
          <label>Email</label>
          <input type="email" class="form-control" v-model="user" placeholder="Ingrese email">
      </div>
      <div class="form-group">
          <label>Contraseña</label>
          <input type="password" class="form-control" v-model="pass" placeholder="Ingrese contraseña">
      </div>
      <div class="form-group">
          <label>Confirmar Contraseña</label>
          <input type="password" class="form-control" v-model="cpass" placeholder="Repita contraseña">
          <template v-if="pass != cpass"><small>Las contraseñas deben coincidir</small></template>
      </div>
      <input type="submit" value="REGISTRAR" class="btn btn-success">
      <router-link to="/login">Tengo una cuenta!</router-link>
    </form>
</div>
</template>
<script>
import firebase from 'firebase'
export default {
  data(){
      return{
          user:'',
          pass:'',
          cpass:''
      }
  },
  methods:{
      registrar(){
          if(this.user!='' && this.pass!='' && this.pass==this.cpass ){
              firebase.auth().createUserWithEmailAndPassword(this.user,this.pass)
              .then(
                  (user)=>{
                      this.$router.replace('admin')
                  },
                  function(err){
                      alert('Ops. ' + err.message)
                  }
              );
          }
      }
  }
}
</script>
<style scoped>
.container{
    width: 500px;
    max-width: 95%;
    background: #eee;
    display: flex;
    align-items: center;
    justify-content: center;
}
form{
    width: 400px;
    max-width: 90%;
    margin: 3em 0;
    
    text-align: left;
}
h3{
    text-align:center;
    margin-bottom: 1em;
    font-weight: bold;
}
.form-group{
    text-align: left;
}
label{
    margin-left: .5em;
    font-weight: bold;
}
input[type="submit"]{
    display: block;
    margin-bottom: 1em;
}
input[type="submit"]:hover{
    cursor: pointer;
}

</style>




