<template>
  <div class="container bloques mt-md-4"> 
      <div class="card">
            <h3 class="card-header">PUERTAS</h3>
            <div class="card-block ml-4">
                <p class="card-text">Controla las puertas  de:</p>
                <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-door-1" class="toggle-button" v-model="door1" @click="setDoor1">
                        <label for="toggle-door-1"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 1</div>
                </div>
                 <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-door-2" class="toggle-button" v-model="door2" @click="setDoor2">
                        <label for="toggle-door-2"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 2</div>
                </div>
                 <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-door-3" class="toggle-button" v-model="door3" @click="setDoor3">
                        <label for="toggle-door-3"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 3</div>
                </div>
                 <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-door-4" class="toggle-button" v-model="door4" @click="setDoor4">
                        <label for="toggle-door-4"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 4</div>
                </div>
            </div>
        </div>     
        <div class="card">
            <h3 class="card-header">LUCES</h3>
            <div class="card-block ml-4">
                <p class="card-text">Controla las luces  de:</p>
                <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-room-1" class="toggle-button" v-model="room1" @click="setRoom1">
                        <label for="toggle-room-1"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 1</div>
                </div>
                 <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-room-2" class="toggle-button" v-model="room2" @click="setRoom2">
                        <label for="toggle-room-2"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 2</div>
                </div>
                 <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-room-3" class="toggle-button" v-model="room3" @click="setRoom3">
                        <label for="toggle-room-3"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 3</div>
                </div>
                 <div class="checkbox">
                    <div>
                        <input type="checkbox" id="toggle-room-4" class="toggle-button" v-model="room4" @click="setRoom4">
                        <label for="toggle-room-4"></label>
                    </div>
                   <div class="toggle-text">Laboratorio 4</div>
                </div>
            </div>
        </div>
  </div>
</template>
<script>
import {db} from '../firebase'
import firebase from 'firebase'
export default {
   
   data(){return{
        door1:'',
        door2:'',
        door3:'',
        door4:'',
        room1:'',
        room2:'',
        room3:'',
        room4:''  
  }},
  created(){
      db.ref('door1').on('value',snapshot=>this.door1=snapshot.val());
      db.ref('door2').on('value',snapshot=>this.door2=snapshot.val());
      db.ref('door3').on('value',snapshot=>this.door3=snapshot.val());
      db.ref('door4').on('value',snapshot=>this.door4=snapshot.val());
      db.ref('room1').on('value',snapshot=>this.room1=snapshot.val());
      db.ref('room2').on('value',snapshot=>this.room2=snapshot.val());
      db.ref('room3').on('value',snapshot=>this.room3=snapshot.val());
      db.ref('room4').on('value',snapshot=>this.room4=snapshot.val());
  },
  methods:{
      setRoom1(){
          let msj = this.room1? 'Apago luz habitacion 1':'Encendio luz habitacion 1';
          db.ref('room1').set(!this.room1);
          this.pushLog(msj);          
      },
      setRoom2(){
          let msj = this.room2? 'Apago luz habitacion 2':'Encendio luz habitacion 2';
          db.ref('room2').set(!this.room2);
          this.pushLog(msj);     
      },
      setRoom3(){
          let msj = this.room3? 'Apago luz habitacion 3':'Encendio luz habitacion 3';
          db.ref('room3').set(!this.room3);
          this.pushLog(msj);     
      },
      setRoom4(){
          let msj = this.room4? 'Apago luz habitacion 4':'Encendio luz habitacion 4';
          db.ref('room4').set(!this.room4);
          this.pushLog(msj);     
      },
       setDoor1(){
          let msj = this.door1? 'Cerro puerta Laboratorio 1':'Abrio puerta Laboratorio 1';
          db.ref('door1').set(!this.door1);
          this.pushLog(msj);          
      },
      setDoor2(){
          let msj = this.door2? 'Cerro puerta Laboratorio 2':'Abrio puerta Laboratorio 2';
          db.ref('door2').set(!this.door2);
          this.pushLog(msj);     
      },
      setDoor3(){
          let msj = this.door3? 'Cerro puerta Laboratorio 3':'Abrio puerta Laboratorio 3';
          db.ref('door3').set(!this.door3);
          this.pushLog(msj);     
      },
      setDoor4(){
          let msj = this.door4? 'Cerro puerta Laboratorio 4':'Abrio puerta Laboratorio 4';
          db.ref('door4').set(!this.door4);
          this.pushLog(msj);     
      },
      pushLog(msj){
          let mail = firebase.auth().currentUser.email;   
          let date = new Date();
          let dia = date.getDate();
          dia=dia<10? '0'+String(dia):dia;
          let mes = parseInt(date.getMonth()) + 1 ;
          mes =mes<10? '0'+String(mes):mes;
          let yyy = date.getFullYear();
          let hora = date.getHours();
          hora=hora<10? '0'+String(hora):hora;
          let min = date.getMinutes();
          min=min<10? '0'+String(min):min;       
          let obj = {
              mail,
              msj,
              fecha: `${dia}-${mes}-${yyy}`,
              hora: `${hora}:${min}`
          }
          db.ref('logs').push(obj);
      }
  }
}
</script>
<style scoped>
.bloques{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}
.card{
    width: 400px;
    text-align: left;
    margin:1em;
    max-width: 90%;
}
.datos{
    display: flex;
}
img{
    width: 5rem;
    height: 5rem;
    margin-right: 2rem;
}
h1{
    font-size: 5rem;
}
.checkbox{
   display: flex;
   height: 1.5rem;;
   margin:1rem 0;
   /* background: #eee; */
}
.toggle-text{
    margin-left: 1rem;
    font-weight: bold;
}
.toggle-button {
  display: none; }
  .toggle-button + label {
    width: 2.5rem;
    height: 1.5rem;
    background: rgba(222, 43, 43, 0.5);
    border-radius: 3rem;
    box-shadow: 0 0 0 2px rgba(238, 238, 238, 0.5);
    transition: all 0.3s; }
    .toggle-button + label:before {
      content: "";
      display: block;
      width: 1.5rem;
      height: 1.5rem;
      border-radius: 50%;
      background: #DE2B2B;
      position: relative;
      left: 0;
      transition: all 0.3s; }
  .toggle-button:checked + label {
    background: rgba(6, 148, 6, 0.5); }
    .toggle-button:checked + label:before {
      left: 1rem;
      background: #069406; }
      @media (max-width: 600px) {
        img {
            width: 3rem;
            height: 3rem;
            margin-right: 1rem;
        }
        h1{
            font-size: 3rem;
        }
        h3{
            font-size: 1.5rem;
        }
    }
</style>



